    url("./assets/fonts/BellGothicStd-Bold.otf") format("truetype");
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: "BellGothicStd";
  src: local("BellGothicStd"),
    url("./assets/fonts/BellGothicStd-Light.otf") format("truetype");
  font-weight: normal;  
